* {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;

  margin: 30px 0;
  display: flex;
  justify-content: center;
}

.cardImg {
  border-radius: 4px;
}

.button {
  background-color: #4aa7ff;
  border: 3px solid #4aa7ff;

  margin-top: 30px;
  padding: 8px 40px;
  display: inline-block;

  line-height: 1.5;
  font-size: 20px;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  transition: background-color 0.2s ease-in;

  border-radius: 50px;

  cursor: pointer;
}

.button:hover {
  background-color: #80c1ff;
}

.button:active {
  background-color: #2cbcff;
}

.button:focus {
  outline: none;
  border: 3px solid #4aa7ff;
  box-shadow: 0px 0px 10px 0 #2cbcff;
}
